footer {
    max-width: 1380px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin: auto;

    .rights {
        img {
            height: 32px;
        }

        div {
            font-size: 14px;
            line-height: 22px;
            margin: 20px 10px;
            color: #727272;
        }

        @media screen and (max-width: 992px) and (min-width: 320px) {
            text-align: center;

            & > * {
                float: none !important;
            }
        }
    }

    .social {
        margin-top: 20px;
        text-align: right;

        a {
            font-size: 14px;
            line-height: 24px;
            color: #bf4cbf;
            font-family: Akkurat-Normal;

            i {
                margin: 0 5px;
                font-size: 18px;
            }
        }

        @media screen and (max-width: 992px) and (min-width: 320px) {
            text-align: center;
        }
    }
}
